/* Modal */
.ReactModalPortal > div {
	opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
	align-items: center;
	display: flex;
	justify-content: center;
	transition: opacity 0.2s ease-in-out;
	z-index: 999;
}

.modalHeader {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.modalClose {
	cursor: pointer;
	color: rgb(180, 34, 34);
}

.modalClose:hover {
	color: rgb(255, 0, 0);
}

.modal-fondo {
	background-color: rgba(0, 0, 0, 0.3);
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
	position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
	opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
	opacity: 0;
}

.calendarmodal {
	position: absolute;
	top: 40px;
	right: 40px;
	bottom: 40px;
	left: 40px;
	border: 1px solid rgb(204, 204, 204);
	background: rgb(255, 255, 255);
	overflow: auto;
	border-radius: 4px;
	outline: none;
	top: 0px;
	left: 40px;
	right: 40px;
	bottom: 0px;
	padding: 20px;
}
