@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;
}

html,
body,
#root {
	height: 100%;
}

button {
	cursor: pointer;
	outline: none;
}

button:focus,
input:focus {
	outline: none;
}

/* datapicker */
.react-datetime-picker__wrapper {
	border: none;
}
/* datapicker */

